<template>
  <div id="collection">
    <header-nav :title="msg"></header-nav>
    <div class="navbar" v-if="list.length > 0 || isShow">
      <div class="navItem" v-for="(item, index) in navList" :key="index">
        <div class="text" @click="setIndex(index)" :class="{active: index === activeIndex}">{{item.tab_name}}</div>
      </div>
    </div>
    <div class="scrollTouch" :class="{active: $config.source_style > 5}" v-infinite-scroll="loadMore" infinite-scroll-distance="200">
      <news-list
        :name="1"
        :list="list"
        :collectionBtn="true"
        v-if="list.length !== 0"
        @deleteFn="deleteFn">
      </news-list>
      <list-loading :page="2" :loading="loading"></list-loading>
      <div v-if="list.length > 0 && loading === 2" class="no-data">没有更多数据!</div>
    </div>
    <div id="noList" ref="noList" v-if="list.length === 0 && loading === 2">
      <stateless-head v-if="activeIndex === 0 || activeIndex === 2" :headData="headData"></stateless-head>
      <stateless-head v-if="activeIndex === 1" :headData="headData1"></stateless-head>
      <news-list :list="recommendList"></news-list>
    </div>
    <!-- 是否取消收藏弹框 -->
    <prompt-box
    :dropBol="dropBol"
    @leftBtn="dropBol=false"
    @rightBtn="dropFn"
    rightTxt="确定"
    :contentTxt="contentTxt"></prompt-box>
  </div>
</template>

<script>
import HeaderNav from '../common/header-nav'
import NewsList from '../common/news-list'
import StatelessHead from '../common/stateless-head'
import PromptBox from '../common/prompt-box'
import { mapState } from 'vuex'
import ListLoading from '@/components/movie/common/list-loading'

export default {
  name: 'collection',
  computed: mapState({
    commonState: state => state.common
  }),
  data () {
    return {
      reloading: 0,
      navList: [{
        tab_name: '全部',
        type: ''
      }, {
        tab_name: '视频',
        type: 'movie'
      }, {
        tab_name: '资讯',
        type: 'article'
      }],
      activeIndex: 0,
      isShow: false,
      type: '',
      msg: '我的收藏',
      list: [],          // 收藏列表
      currentItem: {},
      recommendList: [], // 推荐列表
      headData: {
        imgUrl: require('../../assets/img/icon-collect.png'),
        txt: '还没有收藏的文章'
      },
      headData1: {
        imgUrl: require('../../assets/img/icon-collect.png'),
        txt: '还没有收藏的视频'
      },
      id: 0,               // 被删除文章id
      dropBol: false,      // 取消收藏弹框
      index: null,         // 操作下标
      collectionBol: null, // 取消收藏还是收藏
      contentTxt: '',       // 弹框文字
      page: 1
    }
  },
  methods: {
    // 确定删除
    dropFn () {
      this.dropBol = false
      let data = {}
      if (this.currentItem.type === 'movie') {
        data.vod_id = this.currentItem.aid
        data.type = 'movie'
      } else {
        data.aid = this.currentItem.aid
        data.type = 'article'
      }
      data.status = this.currentItem.collectionBol ? 1 : 2
      let txt = data.status === 2 ? '删除收藏成功' : '收藏成功'
      this.$api.post('CLLECT', data, (res) => {
        this.$toast({
          message: txt,
          position: 'middle',
          duration: 1500
        })
        this.currentItem.collectionBol = !this.currentItem.collectionBol
        this.list.splice(this.index, 1)
        // this.$set(this.list, this.index, this.currentItem)
        // this.$set(this.list[this.index], 'collectionBol', this.collectionBol)
      })
    },
    loadMore () {
      if (this.loading > 0) {
        return
      }
      setTimeout(() => {
        this.loading = 0
      }, 2000)
      this.loading = 1
      this.getList(res => {
        this.list = [...this.list, ...res.data.data]
      })
    },
    // 打开删除收藏记录弹框
    deleteFn (id, collectionBol, index) {
      this.contentTxt = collectionBol ? '是否删除收藏' : '是否确定收藏'
      this.dropBol = true
      this.currentItem = this.list[index]
      this.index = index
    },
    setIndex (index) {
      // window.scrollTo(0, 0)
      this.type = this.navList[index].type
      this.activeIndex = index
      this.list = []
      this.page = 1
      this.loading = 1
      this.getList((res) => {
        this.list = res.data.data
      })
    },
    // 获取收藏列表
    getList (cb) {
      let data =  {
        page: this.page
      }
      if (this.type) {
        data.type = this.type
      }
      this.$api.get('COLLECTION', data, (res) => {
        this.loading = 0
        for (let v of res.data.data) {
          if (v.type === 'article') {
            if (v.item.length === 0) {
              v.item = {
                aid: v.aid,
                title: v.title,
                type: 4
              }
            }
          }
          continue
        }
        cb && cb(res)
        console.log(this.page)
        if (res.data.data.length === 0 && this.page === 1 && !data.type) {
          this.getRecommend()
        }
        if (this.page >= res.data.last_page) {
          this.loading = 2
        }
        this.page++
      })
    },
    // 上拉函数
    upList () {
      // console.log(this.$utils.scrollHeight())
      if (this.$utils.scrollHeight() === 0) {
        if (this.ul.offsetHeight - this.$refs.noList.scrollTop < 500 && this.reloading === 0) {
          this.reloading = 1
          setTimeout(() => {
            this.reloading = 0
          }, 5000)
          let data = {}
          data.columnid = 'index'
          data.lastid = this.recommendList[(this.recommendList.length - 1)].id
          this.$api.get('PULL_UP', data, (res) => {
            this.reloading = 0
            let list = res.data || []
            this.recommendList = [...this.recommendList, ...list]
          })
        }
      }
    },
    // 获取推荐阅读
    getRecommend () {
      let data = {}
      data.columnid = 'index'
      this.$api.get('PULL', data, (res) => {
        this.recommendList = res.data || []
        this.$refs.noList.onscroll = this.upList
        this.$nextTick(() => {
          this.ul = this.$refs.noList.getElementsByTagName('ul')[0]
        })
      })
    }
  },
  created () {
    this.loading = 1
    this.getList(res => {
      this.list = res.data.data
      if (this.list.length > 0) {
        this.isShow = true
      }
    })
  },
  components: {
    HeaderNav,
    NewsList,
    StatelessHead,
    PromptBox,
    ListLoading
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" type="text/css">
#noList {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  height: 100vh;
}
#collection {
  height: 100vh;
  box-sizing: border-box;
  overflow: hidden;
}
.hasHeader {
  .scrollTouch {
    top: 188px;
  }
  .navbar {
    top: 88px;
  }
}
.no-data {
  line-height: 80px;
  text-align: center;
  line-height: 80px;
  color: #999;
  font-size: 30px;
}
.navbar {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #eee;
  position: fixed;
  background: #fff;
  left: 0;
  right: 0;
  z-index: 1;
  >div {
    flex: 1;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    .text {
      padding: 20px 20px 16px;
      line-height: 45px;
      border-bottom: 4px solid #fff;
      font-size: 32px;
      &.active {
        border-color: #F4A82B;
        color:  #F4A82B;
      }
    }
  }
}
.scrollTouch {
  position: absolute;
  top: 88px;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  padding-bottom: 100px;
  box-sizing: border-box;
}
</style>



